.Card {
    background-color: rgba(31, 159, 195, 0.5);
    border-radius: 10px;
    overflow: hidden;
    border: 3px groove rgba(31, 159, 195, 0.5) thin;
    width: 600px;
    margin: auto;
    
}

.Card .Titulo {
    padding: 5px 0;
    display: flex;
    justify-content: center;
}

.Card .Conteudo {
    background-color: white;
    color: black;
    padding: 10px;
    width: 100%;
    
}